.popup {
    display: flex;
    visibility: hidden;
    position: fixed; 
    top: 0;
    right: 0; 
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    align-items: center;
    justify-content: center;
    font-family: 'Inter', Arial, sans-serif;
    z-index: 2;
    opacity: 0;
    transition: visibility 0.5s, opacity 0.5s ease;
}